ol {
    margin: 0;
    padding-left: 25px;
    line-height: 1.8;

    &.chinese {
        padding-left: 30px;
        list-style-type: none;

        &>li {
            position: relative;

            &::before {
                content: '(' attr(data-num) ')';
                position: absolute;
                top: 0;
                left: -3px;
                transform: translateX(-100%);
            }
        }
    }

    &.decimal {
        list-style-type: none;

        &>li {
            position: relative;
            counter-increment: decimal;

            &::before {
                content: '(' counter(decimal, decimal) ')';
                position: absolute;
                top: 0;
                left: -3px;
                transform: translateX(-100%);
            }
        }
    }

    @include breakpoint-down('mobile') {
        padding-left: 20px;

        &.chinese {
            padding-left: 25px;
        }
    }
}