.mobileOnly {
    @include breakpoint-up('laptop') {
        display: none;
    }
}

.desktopOnly {
    @include breakpoint-down('tablet') {
        display: none;
    }
}

.block-wrap.mobileOnly {
    @include breakpoint-up('laptop') {
        display: block !important;
        height: 0 !important;
        margin: 0 !important;
        padding: 0 !important;
        overflow: hidden !important;
        opacity: 0 !important;
        pointer-events: none !important;
    }
}

.block-wrap.desktopOnly {
    @include breakpoint-down('tablet') {
        display: block !important;
        height: 0 !important;
        margin: 0 !important;
        padding: 0 !important;
        overflow: hidden !important;
        opacity: 0 !important;
        pointer-events: none !important;
    }
}