.notice {
    margin-top: 65px;
    padding: 0 30px;
    border-radius: 10px;
    border: solid 3px #4179ce;
    background-color: #fff;

    &-action {
        position: relative;
        display: block;
        padding: 28px 0;
        font-size: 20px;
        font-weight: bold;
        color: #4179ce;
        text-decoration: none;

        &::after {
            content: '＋';
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
            display: block;
            font-size: 1.2em;
            transition: .3s;
        }

        &.open {
            &::after {
                content: '—';
                transform: translateY(-50%) rotate(180deg);
            }
        }
    }

    &-content {
        display: none;
        padding: 25px 0;
        font-size: 16px;
        border-top: 2px solid #4179ce;
    }

    @include breakpoint-down('mobile') {
        margin-top: 50px;
        padding: 0 20px;
        border: solid 2px #4179ce;

        &-action {
            padding: 20px 0;
            font-size: 16px;
        }

        &-content {
            padding: 20px 0;
            font-size: 14px;
            border-top: 1px solid #4179ce;
        }
    }
}