h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-size: 1em;
}

p {
    margin: 0;
}

b {
    font-weight: 500;
}

.coinBox {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    max-width: 800px;
    margin: 0 auto;
    padding: 7px 30px;
    border-radius: 25.5px;
    background-color: #e6f0fb;

    &-icon {
        flex: 0 0 auto;
    }

    &-text {
        flex: 1;
        margin-left: 10px;
        font-weight: 500;
        letter-spacing: 1.5px;
        text-align: left;

        span {
            font-size: 20px;
            color: #ea6000;
        }
    }

    @include breakpoint-down('tablet') {
        max-width: 560px;
        padding: 10px;
        border-radius: 8px;

        &-icon {
            img {
                width: 25px;
            }
        }

        &-text {
            margin-left: 8px;
            font-size: 14px;
            letter-spacing: 0;
        }

        span {
            font-size: 14px;
        }
    }
}