@keyframes scroll {
    0% {
        transform: translate(-50%, 0);
    }

    100% {
        transform: translate(-50%, 80%);
    }
}

@keyframes eyes {
    0% {
        transform: scaleY(1);
    }

    10% {
        transform: scaleY(0);
    }

    20% {
        transform: scaleY(1);
    }

    100% {
        transform: scaleY(1);
    }
}

@keyframes hand {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(3deg);
    }
}

@keyframes man {
    0% {
        transform: skewX(-2deg);
    }

    100% {
        transform: skewX(2deg);
    }
}

@keyframes debut {
    0% {
        transform: translateY(100px);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.banner {
    position: relative;
    padding-bottom: 42%;
    background-image: url(../images/banner/bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    overflow: hidden;

    img {
        width: 100%;
    }

    &-item {
        position: absolute;
    }

    &-title {
        top: 3%;
        left: 28%;
        width: 46%;
    }

    &-eyes {
        top: 15%;
        left: 43%;
        width: 3.7%;

        img {
            animation-name: eyes;
            animation-duration: 1.5s;
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    &-light {
        top: 14%;
        left: 43%;
        width: 3.7%;
    }

    &-subTitle {
        top: 31%;
        left: 36.5%;
        width: 26%;
    }

    &-text {
        top: 39%;
        left: 0;
        box-sizing: border-box;
        width: 100%;
        padding: 0 20px;
        font-size: 0.9375vw;
        text-align: center;
        z-index: 1;

        span {
            color: #ea6000;
        }

        b {
            font-size: 1.25vw;
        }
    }

    &-man {
        left: 37%;
        bottom: -16%;
        width: 22%;
        opacity: 0;
        animation-name: debut;
        animation-duration: .8s;
        animation-timing-function: cubic-bezier(0, .56, .6, 1.57);
        animation-fill-mode: forwards;

        img {
            animation-name: man;
            animation-duration: 2s;
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    &-group {
        left: 14%;
        bottom: -13.5%;
        width: 68%;
        opacity: 0;
        animation-name: debut;
        animation-duration: .8s;
        animation-timing-function: cubic-bezier(0, .56, .6, 1.57);
        animation-fill-mode: forwards;
        animation-delay: .8s;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-image: url(./../images/banner/group-mask.png);
            background-size: 100%;
        }
    }

    &-telescope {
        top: 17%;
        left: 0;
        width: 33%;

        img {
            transform-origin: top right;
            animation-name: hand;
            animation-duration: 2s;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    &-loud {
        top: 48%;
        left: 25%;
        width: 20%;

        img {
            transform-origin: top right;
            animation-name: hand;
            animation-duration: 2s;
            animation-delay: -2s;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    &-phone {
        bottom: 32%;
        right: 33%;
        width: 14%;

        img {
            transform-origin: top left;
            animation-name: hand;
            animation-duration: 2s;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    &-magnifier {
        top: 0;
        right: 0;
        width: 33%;

        img {
            transform-origin: center left;
            animation-name: hand;
            animation-duration: 2s;
            animation-delay: -2s;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    &-scroll {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 50%);
        width: 100px;
        height: 100px;
        display: block;
        border-radius: 50%;
        background-color: #4179ce;

        &::after {
            content: '';
            position: absolute;
            top: 15px;
            left: 50%;
            transform: translate(-50%, 0);
            display: block;
            width: 33px;
            height: 18px;
            background-image: url(./../images/icon/arrow.png);
            background-repeat: no-repeat;
            animation-name: scroll;
            animation-duration: .8s;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    @media only screen and (max-width: 1700px) {
        &-eyes {
            top: 14.5%;
        }

        &-light {
            top: 13.5%;
        }
    }

    @media only screen and (max-width: 1350px) {
        &-eyes {
            top: 14%;
        }

        &-light {
            top: 13%;
        }
    }

    @media only screen and (max-width: 1180px) {
        &-eyes {
            top: 13.5%;
        }

        &-light {
            top: 12.5%;
        }
    }

    @media only screen and (max-width: 1023px) {
        &-eyes {
            top: 15.5%;
        }

        &-light {
            top: 15%;
        }
    }

    @media only screen and (max-width: 580px) {
        &-eyes {
            top: 15%;
        }

        &-light {
            top: 14.5%;
        }
    }

    @media only screen and (max-width: 450px) {
        &-eyes {
            top: 14.3%;
        }

        &-light {
            top: 14%;
        }
    }

    @include breakpoint-down('tablet') {
        padding-bottom: 158%;
        background-image: url(../images/banner/bg-mb.jpg);

        &-title {
            top: 3.5%;
            left: 12.5%;
            width: 80%;
        }

        &-eyes {
            // top: 14.3%;
            left: 56.8%;
            width: 10%;
        }

        &-light {
            // top: 14%;
            left: 56.8%;
            width: 10%;
        }

        &-subTitle {
            top: 42%;
            left: 13%;
            width: 72.5%;
        }

        &-text {
            top: 49%;
            font-size: 3.4375vw;
            text-align: left;

            b {
                font-size: 4vw;
            }
        }

        &-man {
            left: 22%;
            bottom: -5%;
            width: 50%;
        }

        &-group {
            left: 5%;
            bottom: -11.7%;
            width: 93%;

            &::after {
                background-image: url(./../images/banner/group-mask-mb.png);
            }
        }

        &-telescope {
            top: 13%;
            left: 1%;
            width: 32%;
        }

        &-loud {
            top: 41%;
            left: 14.5%;
            width: 32%;
        }

        &-phone {
            bottom: 42%;
            right: 26%;
            width: 22%;
        }

        &-magnifier {
            top: 3.5%;
            right: 5%;
            width: 36%;
        }

        &-scroll {
            width: 55px;
            height: 55px;

            &::after {
                content: '';
                top: 9px;
                width: 18px;
                height: 9.5px;
                background-size: 100%;
            }
        }
    }
}