@import "./_variables";
@import "./_mixins";

html,
body {
    height: 100%;
}


.container {
    min-height: 100%;
    overflow: hidden;
}

/* ------------------------------- Header ------------------------------- */

.header {
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    background-image: url(../images/background/header.png);
    background-repeat: no-repeat;
    background-position: center right;
    background-color: white;
    z-index: 100;

    .yuantaplusLogo {
        width: 130px;
        height: 39px;
        background-image: url(../images/logo/yuantaplus.svg);
        background-repeat: no-repeat;
    }

    @include breakpoint-down('tablet') {
        padding: 16px;

        .yuantaplusLogo {
            width: 120px;
            height: 36px;
        }
    }

    @include breakpoint-down('mobile') {
        .yuantaplusLogo {
            width: 87px;
            height: 26px;
        }
    }
}

.logo {
    display: inline-block;
    width: 308px;

    img {
        width: 100%;
    }

    @include breakpoint-down('tablet') {
        width: 280px;
    }

    @include breakpoint-down('mobile') {
        width: 203px;
    }
}

/* ------------------------------- Main ------------------------------- */

.main {
    margin-top: 80px;

    @include breakpoint-down('tablet') {
        margin-top: 59.5px;
    }
}

/* ------------------------------- Footer ------------------------------- */

.appDownload {
    display: flex;
    justify-content: center;
    margin-top: 100px;

    &-QRcode {
        img {
            width: 100px;
            margin-right: 20px;
        }
    }

    &-link {

        h5 {
            margin-bottom: 13px;
            font-size: 15px;
            line-height: 19px;
            text-align: center;
        }

        a {
            display: inline-block;
            padding: 0 3px;
            text-decoration: none;
            transition: .3s;
        }

        img {
            width: 170px;
        }
    }

    @include breakpoint-up('laptop') {
        &-link {
            a:hover {
                transform: translateY(-10%);
            }
        }
    }

    @include breakpoint-down('tablet') {
        margin-top: 50px;

        &-QRcode {
            display: none;
        }

        &-link {
            img {
                width: 140px;
            }
        }
    }
}

.footer {
    box-sizing: border-box;
    padding: 10px 12px;
    font-size: 13px;
    font-weight: 300;
    line-height: 2.3;
    text-align: center;
    color: #ffffff;
    background-color: #007fe0;

    &-wrap {
        span+span {
            margin-left: 10px;
        }
    }

    @include breakpoint-down('mobile') {
        padding: 10px 20px 60px;
        font-size: 12px;
        line-height: 1.6;
        text-align: left;

        &-wrap {
            margin-top: 10px;

            span {
                display: block;
            }

            span+span {
                margin-left: 0;
            }
        }
    }
}

/* ------------------------------- Floating tool ------------------------------- */

.mediaIcons {
    position: fixed;
    bottom: 50%;
    right: 10px;
    transform: translateY(50%);
    display: none;
    margin: 0;
    padding: 0;
    z-index: 20;

    li {
        margin: 20px 10px;
        list-style: none;

        a {
            display: block;
            width: 33px;
            height: 33px;
            background-position: center;
            background-size: 100%;
            background-repeat: no-repeat;

            &.facebook {
                background-image: url(../images/icon/social/socialmedia_facebook_grey.png);
            }

            &.line {
                background-image: url(../images/icon/social/socialmedia_line_grey.png);
            }

            &.instagram {
                background-image: url(../images/icon/social/socialmedia_instagram_grey.png);
            }

            &.youtube {
                background-image: url(../images/icon/social/socialmedia_youtube_grey.png);
            }

            &.telegram {
                background-image: url(../images/icon/social/socialmedia_telegram_grey.png);
            }
        }
    }

    @include breakpoint-up('laptop') {
        li {
            a:hover {
                opacity: 1;

                &.facebook {
                    background-image: url(../images/icon/social/socialmedia_facebook.png);
                }

                &.line {
                    background-image: url(../images/icon/social/socialmedia_line.png);
                }

                &.instagram {
                    background-image: url(../images/icon/social/socialmedia_instagram.png);
                }

                &.youtube {
                    background-image: url(../images/icon/social/socialmedia_youtube.png);
                }

                &.telegram {
                    background-image: url(../images/icon/social/socialmedia_telegram.png);
                }
            }
        }
    }

    @include breakpoint-down('tablet') {
        right: 0;
    }

    @include breakpoint-down('mobile') {
        bottom: 0;
        right: 0;
        transform: none;
        width: 100%;
        padding: 4px 0;
        text-align: center;
        background-color: rgba(0, 159, 232, 0.9);

        li {
            display: inline-block;
            vertical-align: middle;
            margin: 6px;

            a {
                width: 28px;
                height: 28px;
                opacity: 1;

                &.facebook {
                    background-image: url(../images/icon/social/socialmedia_facebook.svg);
                }

                &.line {
                    background-image: url(../images/icon/social/socialmedia_line.svg);
                }

                &.instagram {
                    background-image: url(../images/icon/social/socialmedia_instagram.svg);
                }

                &.youtube {
                    background-image: url(../images/icon/social/socialmedia_youtube.svg);
                }

                &.telegram {
                    background-image: url(../images/icon/social/socialmedia_telegram.svg);
                }
            }
        }
    }
}

.gotop {
    position: fixed;
    bottom: 36px;
    right: 36px;
    width: 50px;
    height: 50px;
    display: none;
    border-radius: 25px;
    opacity: .7;
    overflow: hidden;
    z-index: 50;

    &:hover {
        opacity: 1;
    }

    @include breakpoint-down('mobile') {
        width: 40px;
        height: 40px;
        bottom: 70px;
        right: 20px;
    }
}

/* ------------------------------- creditSlogan ------------------------------- */
.creditSlogan {
    width: 100%;
    height: auto;
    background: #ffffff;
    padding: 25px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}

.creditSlogan div {
    font-size: 14px;
    color: #575757;
    padding: 0 0 0 30px;
    line-height: 1.6;
}

.creditSlogan div:first-child {
    font-size: 28px;
    font-weight: 500;
    color: #575757;
    letter-spacing: 0.6px;
    border-right: 1px solid #666666;
    padding: 0 30px 0 0;
}

.creditSlogan div span:first-child::after {
    content: '：';
}

.creditSlogan div:first-child span:first-child {
    padding: 0 10px 0 0;
}

.creditSlogan div:first-child span:first-child::after {
    display: none;
}

.creditSlogan a {
    color: #007fe0;
}

.creditSlogan a:hover {
    color: #007fe0;
}

@media screen and (max-width: 1200px) {
    .creditSlogan div {
        font-size: 14px;
        padding: 0 0 0 10px;
    }

    .creditSlogan div:first-child {
        padding: 0 10px 0 0;
    }
}

@media screen and (max-width: 1024px) {
    .topView h2 {
        width: 100%;
        position: absolute;
        top: 30%;
    }

    .creditSlogan div {
        font-size: 14px;
        padding: 0 0 0 10px;
    }

    .creditSlogan div:first-child {
        box-sizing: border-box;
        padding: 0;
    }

    .creditSlogan div:first-child span {
        display: block;
    }
}

@media screen and (max-width: 870px) {
    .creditSlogan {
        height: 125px;
        flex-direction: column;
        flex-wrap: wrap;
        align-content: center;
    }

    .creditSlogan div {
        align-self: flex-start;
    }

    .creditSlogan div:first-child {
        font-size: 20px;
        margin: 0;
    }

    .creditSlogan p {
        font-size: 14px;
        line-height: 22px;
    }
}

@media screen and (max-width: 800px) {
    .creditSlogan {
        width: 100%;
        height: auto;
        padding: 20px;
    }

    .creditSlogan div {
        width: 100%;
        align-self: center;
        text-align: center;
    }

    .creditSlogan div:first-child {
        font-size: 30px;
        border-bottom: 1px solid;
        border-right: none;
        display: flex;
        justify-content: center;
        margin: 0 0 10px;
    }

    .creditSlogan p {
        font-size: 14px;
        line-height: 2;
        padding: 0;
    }
}

@media screen and (max-width: 420px) {
    .creditSlogan {
        padding: 20px 10px;
    }

    .creditSlogan div:first-child {
        font-size: 24px;
    }

    .creditSlogan p {
        font-size: 12px;
        line-height: 1.8;
    }
}