.wrap {
    position: relative;
    box-sizing: border-box;
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 20px;
}

.sky {
    background-image: linear-gradient(to top, #70a9e7, #4179ce);
}

.cloud {
    padding: 100px 0 70px;
    background-color: #d2e7fa;

    @include breakpoint-down('tablet') {
        padding: 50px 0;
    }
}

@keyframes shine {
    0% {
        transform: rotate(0);
    }

    50% {
        transform: rotate(720deg);
    }

    100% {
        transform: rotate(720deg);
    }
}

.block {
    padding-top: 105px;

    &-wrap {
        position: relative;
        box-sizing: border-box;
        max-width: 1100px;
        margin: 0 auto;
        padding: 0 20px;
    }

    &-title {
        margin-bottom: 35px;
        text-align: center;

        span {
            display: inline-block;
            vertical-align: middle;
            margin: 0 7px 0 10px;
            font-size: 50px;
            font-weight: 500;
            letter-spacing: 2.5px;
            color: #fff;
        }

        &::before,
        &::after {
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 28px;
            height: 28px;
            background-image: url(./../images/icon/shine.png);
            background-repeat: no-repeat;
            background-size: contain;
            animation-name: shine;
            animation-duration: 2.5s;
            animation-iteration-count: infinite;
        }
    }

    &-text {
        margin-bottom: 50px;
        text-align: center;
    }

    &-content {
        padding-top: 20px;
    }

    @include breakpoint-down('tablet') {
        padding-top: 70px;

        &-title {
            margin-bottom: 20px;

            span {
                margin: 0 2px 0 5px;
                font-size: 26px;
            }

            &::before,
            &::after {
                width: 15px;
                height: 15px;
            }
        }

        &-text {
            margin-bottom: 30px;
            font-size: 14px;
        }

        &-content {
            padding-top: 10px;
        }
    }
}

@keyframes shake {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(5deg);
    }
}

.block {
    &.-task & {

        &-wrap {
            max-width: 1300px;

            &::before {
                content: '';
                position: absolute;
                top: 100px;
                left: -50px;
                width: 193px;
                height: 193px;
                background-image: url(./../images/background/coins.png);
                background-repeat: no-repeat;
            }

            &::after {
                content: '';
                position: absolute;
                top: 50%;
                right: -130px;
                transform: translateY(-50%);
                width: 252px;
                height: 217px;
                background-image: url(./../images/background/pen.png);
                background-repeat: no-repeat;
            }
        }

        &-bg {
            margin-top: 60px;
            padding: 90px 50px 70px;
            border: 50px solid #0d5199;
            border-radius: 30px;
            background-color: #d2e7fa;

            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%, -45%);
                width: 346px;
                height: 122px;
                background-image: url(./../images/background/clip.png);
                background-repeat: no-repeat;
            }
        }

        &-title {
            span {
                color: #0d5199;
            }
        }
    }

    &.-lottery {
        position: relative;
        padding-bottom: 20px;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            min-width: 1920px;
            height: 100%;
            background-image: url(./../images/background/flag.png);
            background-repeat: no-repeat;
            background-position: top center;
            background-size: 100%;
        }

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            min-width: 1920px;
            height: 100%;
            background-image: url(./../images/background/wave.png);
            background-repeat: no-repeat;
            background-position: bottom center;
            background-size: 100%;
        }
    }

    &.-lottery & {
        &-wrap {
            z-index: 1;

            &::before {
                content: '';
                position: absolute;
                bottom: 250px;
                left: -140px;
                width: 87px;
                height: 220px;
                background-image: url(./../images/background/ribbon-left.png);
                background-repeat: no-repeat;
                background-size: 100%;
                animation-name: shake;
                animation-duration: 1s;
                animation-delay: -1s;
                animation-iteration-count: infinite;
                animation-direction: alternate;
            }

            &::after {
                content: '';
                position: absolute;
                bottom: 290px;
                right: -140px;
                width: 51px;
                height: 205px;
                background-image: url(./../images/background/ribbon-right.png);
                background-repeat: no-repeat;
                background-size: 100%;
                animation-name: shake;
                animation-duration: 1s;
                animation-iteration-count: infinite;
                animation-direction: alternate;
            }
        }

        &-content {
            box-sizing: border-box;
            padding: 55px 70px 65px;
            border-radius: 30px;
            border: 2px solid #fff;
            background-color: #366cbe;
        }
    }

    @include breakpoint-down('tablet') {
        &.-task {
            position: relative;
            margin-top: 190px;
            padding-top: 40px;
            padding-bottom: 40px;
            background-color: #d2e7fa;
            z-index: 1;

            &::before {
                content: '';
                position: absolute;
                bottom: 100%;
                left: 50%;
                transform: translateX(-50%);
                width: 1023px;
                height: 106px;
                background-image: url(./../images/background/wave-top.png);
            }

            &::after {
                content: '';
                position: absolute;
                top: 100%;
                left: 50%;
                transform: translateX(-50%);
                width: 1023px;
                height: 106px;
                background-image: url(./../images/background/wave-bottom.png);
            }
        }

        &.-task & {
            &-wrap {
                &::before {
                    top: -110px;
                    left: 50px;
                    width: 48px;
                    height: 48px;
                    background-image: url(./../images/background/coins-mb.png);
                    background-size: 100%;
                }

                &::after {
                    top: -130px;
                    right: -50px;
                    transform: none;
                    width: 120px;
                    height: 105px;
                    background-size: 100%;
                }
            }

            &-bg {
                margin-top: 0;
                padding: 0;
                border: none;
                border-radius: 0;
                background-color: transparent;

                &::after {
                    content: none;
                }
            }
        }

        &.-lottery {
            padding-top: 155px;
            padding-bottom: 110px;
            background-color: #4179ce;

            &::before {
                min-width: 1023px;
                background-image: url(./../images/background/flag-mb.png);
                background-position: top 52px center;
            }

            &::after {
                min-width: 1023px;
                background-image: url(./../images/background/wave-top.png);
            }
        }

        &.-lottery & {
            &-wrap {
                &::before {
                    top: 120px;
                    bottom: auto;
                    left: calc(50% - 140px);
                    width: 33px;
                    height: 83px;
                }

                &::after {
                    top: 50px;
                    bottom: auto;
                    left: calc(50% + 120px);
                    width: 20px;
                    height: 77px;
                }
            }

            &-content {
                padding: 0;
                border-radius: 0;
                border: none;
                background-color: transparent;
            }
        }
    }
}

.puzzleGrid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 8px;

    &-item {
        flex: 0 0 auto;
        width: 376px;
        margin: -8px;
    }

    @include breakpoint-down('laptop') {
        margin: 12px;

        &-item {
            margin: -12px;
        }
    }

    @include breakpoint-down('tablet') {
        display: block;
        max-width: 450px;
        margin: 0 auto;

        &-item {
            width: 265px;
            margin: 0;
            margin-left: auto;
            margin-right: -15px;

            &:nth-of-type(even) {
                margin-left: -15px;
                margin-right: auto;
            }

            &+& {
                margin-top: 25px;
            }

            &:nth-of-type(3) {
                margin-top: -10px;
            }
        }
    }
}

@keyframes fly {
    0% {
        transform: translateY(-5px);
    }

    100% {
        transform: translateY(5px);
    }
}

.puzzle {
    position: relative;

    &-pic {
        position: absolute;
        top: -7px;
        left: 50px;
        transform: translateX(-100%);
        z-index: 1;
        pointer-events: none;

        img {
            animation-name: fly;
            animation-duration: 2s;
            animation-timing-function: ease-in-out;
            animation-iteration-count: infinite;
            animation-direction: alternate;
        }
    }

    &-content {
        position: relative;
        box-sizing: border-box;
        width: 376px;
        height: 376px;
        padding: 60px 100px 20px 50px;
        color: #fff;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-image: url(./../images/background/puzzle01.png);
            background-size: 100%;
            pointer-events: none;
        }
    }

    &-title {
        display: inline-block;
        padding-bottom: 5px;
        font-size: 30px;
        font-weight: 500;
        border-bottom: 1px solid #fff;
    }

    &-text {
        height: 140px;
        margin: 17px 0 12px;
        font-size: 20px;
        line-height: 1.4;

        span {
            color: #fff2ad;
        }

        b {
            color: #ffba00;
        }

        .note {
            display: inline-block;
            font-size: 14px;
        }
    }

    &-btn {
        display: inline-block;
        padding: 1px 20px 4px;
        font-size: 18px;
        text-decoration: none;
        color: #fff;
        border-radius: 17px;
        background-color: #ea6000;

        img {
            margin-right: 4px;
        }

        span {
            display: inline-block;
            vertical-align: middle;
        }

        .go {
            margin-left: 5px;
            padding: 0 9px 1px 9px;
            font-size: 12px;
            color: #ea6000;
            border-radius: 10px;
            background-color: #fff;
        }

        &.-gn {
            background-color: #3e7457;

            .go {
                color: #3e7457;
            }
        }
    }

    @include breakpoint-up('laptop') {
        &-btn {
            transition: .3s;

            &:hover {
                transform: scale(1.1);
            }
        }
    }

    @include breakpoint-down('tablet') {
        &-pic {
            top: -3px;
            left: 40px;

            img {
                width: 230px;
            }
        }

        &-content {
            width: 265px;
            height: 265px;
            padding: 35px 60px 40px 30px;
        }

        &-title {
            padding-bottom: 3px;
            font-size: 20px;
        }

        &-text {
            height: 105px;
            margin: 10px 0 5px;
            font-size: 15px;

            .note {
                font-size: 12px;
            }
        }

        &-btn {
            padding: 1px 17px 3px;
            font-size: 14px;

            img {
                width: 11px;
                margin-right: 2px;
            }

            .go {
                margin-left: 3px;
                padding: 0 5px 1px 5px;
                font-size: 12px;
                line-height: 1.2;
            }
        }
    }
}

.puzzle {
    &.-right & {
        &-pic {
            left: auto;
            right: 50px;
            transform: translateX(100%);

            img {
                animation-delay: -1s;
            }
        }

        &-content {
            padding: 60px 40px 20px 100px;

            &::before {
                background-image: url(./../images/background/puzzle02.png);
            }
        }
    }

    &.-bottom & {
        &-pic {
            top: auto;
            bottom: -50px;
            left: 60px;

            img {
                animation-delay: -1.5s;
            }
        }

        &-content {
            padding: 100px 75px 20px 50px;

            &::before {
                background-image: url(./../images/background/puzzle02.png);
                transform: rotate(180deg);
            }
        }
    }

    &.-right.-bottom & {
        &-pic {
            left: auto;
            right: 80px;

            img {
                animation-delay: -.5s;
            }
        }

        &-content {
            padding: 100px 40px 20px 110px;

            &::before {
                background-image: url(./../images/background/puzzle01.png);
            }
        }
    }

    @include breakpoint-down('laptop') {
        &.-right.-bottom & {
            &-pic {
                right: 140px;
                bottom: -120px;
            }
        }
    }

    @include breakpoint-down('tablet') {
        &.-right & {
            &-pic {
                right: 40px;
            }

            &-content {
                padding: 35px 30px 40px 60px;
            }
        }

        &.-bottom & {
            &-pic {
                left: 50px;
                bottom: -8px;
            }

            &-content {
                padding: 65px 40px 15px 30px;
            }
        }

        &.-right.-bottom & {
            &-pic {
                right: 105px;
                bottom: -90px;
            }

            &-content {
                padding: 65px 25px 15px 70px;
            }
        }
    }
}

.taskGrid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: -60px -5px;

    &-item {
        margin: 60px 5px;
    }

    @include breakpoint-down('tablet') {
        display: block;
        margin: 0;

        &-item {
            margin: 0;
        }
    }
}

.task {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    text-align: center;

    &-pic {
        img {
            width: 252px;
        }
    }

    &-title {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 12px;
        font-size: 22px;
        font-weight: 500;
        color: #0d5199;

        .num {
            box-sizing: border-box;
            width: 23px;
            height: 23px;
            margin-right: 10px;
            padding-top: 5px;
            line-height: 1;
            font-size: 12px;
            color: #fff;
            border-radius: 50%;
            background-color: #0d5199;
        }
    }

    &-subTitle {
        margin-bottom: 17px;
        font-weight: 500;
        color: #ea6000;

        span {
            font-size: 12px;
        }
    }

    &-text {
        flex: 1 0 auto;
        display: flex;
        align-items: center;
        margin: 20px 0;
    }

    &-btn {
        padding: 11px 23px;
        font-size: 18px;
        color: #fff;
        text-decoration: none;
        border-radius: 25px;
        background-color: #4179ce;
    }

    &-note {
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        width: 150px;
        margin-top: 15px;
        font-size: 12px;
        color: #0d5199;
        text-align: left;
    }

    @include breakpoint-up('laptop') {
        &-btn {
            transition: .3s;

            &:hover {
                transform: scale(1.1);
            }
        }
    }

    @include breakpoint-down('tablet') {
        &-title {
            margin-bottom: 8px;
            font-size: 20px;

            .num {
                width: 21px;
                height: 21px;
                padding-top: 4px;
            }
        }

        &-subTitle {
            margin-bottom: 15px;
        }

        &-text {
            margin: 17px 0;
            font-size: 15px;
        }

        &-btn {
            font-size: 16px;
        }

        &-note {
            transform: translateX(-50%) scale(0.9);
            width: 100%;
            text-align: center;
        }
    }
}

.lotteryGrid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: -40px -25px;

    &-item {
        flex: 0 0 auto;
        margin: 40px 25px;
    }

    &-wrap {
        flex: 0 0 auto;
        width: 100%;
    }

    @include breakpoint-down('tablet') {
        margin: -20px 0;

        &-item {
            width: 50%;
            margin: 20px 0;
        }
    }
}

.lottery {
    text-align: center;

    &-title {
        margin-top: 40px;
        font-size: 24px;
        font-weight: 500;
        color: #ffb900;
    }

    &-text {
        margin-top: 5px;
        font-size: 18px;
        font-weight: 500;
        color: #fff;
    }

    @include breakpoint-down('tablet') {
        &-pic {
            img {
                width: 160px;
            }
        }

        &-title {
            margin-top: 15px;
            font-size: 18px;
        }

        &-text {
            font-size: 14px;
        }
    }
}

.giftGrid {
    max-width: 1025px;
    margin: 0 auto;

    &-title {
        margin-bottom: 30px;
        font-size: 32px;
        font-weight: 500;
        color: #005bab;
        text-align: center;

        span {
            font-size: 44px;
            color: #ea6000;
        }
    }

    &-content {
        display: flex;
    }

    &-item {
        flex: 1;
        padding: 0 60px 40px;

        &+& {
            border-left: 1px solid #4179ce;
        }
    }

    @include breakpoint-down('tablet') {
        &-title {
            margin-bottom: 30px;
            font-size: 24px;

            span {
                font-size: 30px;
            }
        }

        &-content {
            margin: 0 -20px;
        }

        &-item {
            padding: 0 20px 10px;
        }
    }
}

.gift {
    text-align: center;

    &-title {
        margin: 20px 0;
        font-size: 30px;
        font-weight: 500;
        color: #ea6000;

        span {
            display: inline-block;
            vertical-align: middle;
        }

        .type {
            margin-right: 20px;
            padding: 3px 32px 4px;
            font-size: 24px;
            color: #4179ce;
            border-radius: 23px;
            border: solid 2px #4179ce;
        }
    }

    &-text {
        font-size: 18px;
        text-align: left;

        b {
            color: #ea6000;
        }
    }

    @include breakpoint-down('tablet') {
        &-pic {
            img {
                width: 120px;
            }
        }

        &-title {
            margin: 15px 0 8px;
            font-size: 20px;

            span {
                display: block;
            }

            .type {
                display: inline-block;
                margin-right: 0;
                margin-bottom: 5px;
                padding: 3px 22px 4px;
                font-size: 16px;
                border: solid 1px #4179ce;
            }
        }

        &-text {
            font-size: 14px;
        }
    }
}