.action {
    display: flex;
    justify-content: center;

    &-item {
        flex: 0 1 350px;
        max-width: 350px;

        &+& {
            margin-left: 37px;
        }
    }

    @include breakpoint-down('mobile') {
        display: block;

        &-item {
            max-width: 210px;
            margin: auto;

            &+& {
                margin-top: 20px;
                margin-left: auto;
            }
        }
    }
}

.btn {
    position: relative;
    display: block;
    border-radius: 44px;
    cursor: pointer;

    &:hover {
        img {
            transform: translateY(8px);
        }
    }

    @include breakpoint-down('mobile') {

        &:hover {
            img {
                transform: none;
            }
        }
    }
}